@import url(https://fonts.googleapis.com/css?family=Halant|Lora|Rasa&display=swap);
h1.page-title {
  text-align: center;
  padding: 0 1rem;
  font-family: 'Lora', serif;
}

.book {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  background: #2c2c2c;
  color: white;
  border-radius: 10px;
  padding: 1rem;
  margin: 1rem;
}

.book h5,
h6 {
  margin: 0 0 0.5rem 0;
}

.book h6.book-tag-title {
  margin: 0.2rem 0;
}

.book h6.book-author::before {
  content: 'By: ';
}

/* Toggle */

.book-toggle-title {
  margin-top: 1.5rem;
}

.book-toggle {
  margin: 1.5rem 0;
}

/* Tags */

.book ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.book li {
  display: inline;
  font-size: 1.1rem;
}

.book li::after {
  content: ', ';
}

.book li:last-child::after {
  content: '';
}

.button {
  border: transparent;
  border-radius: 5px;
  padding: 0.1rem 0.4rem;
  cursor: pointer;
  font-family: 'Lora', serif;
  font-weight: 600;
  font-size: 1rem;
  color: #2c2c2c;
  background: white;
  outline: none;
}

button.toggle.active {
  background: #bf2e2e;
  color: white;
}

h3.page-subtitle {
  padding: 0 1rem;
  font-family: 'Rasa', serif;
  text-align: center;
}

form {
  display: flex;
  flex-direction: column;
}

form > label {
  font-family: 'Rasa', serif;
  font-weight: bold;
  color: #2c2c2c;
}

form > input {
  margin-bottom: 1.5rem;
  border: 2px solid #2c2c2c;
  border-radius: 5px;
  padding: 0.5rem 0.2rem;
  outline: none;
}

.form-submit {
  border: transparent;
  cursor: pointer;
  font-family: 'Rasa', serif;
  font-size: 1.2rem;
  color: white;
  background: #2c2c2c;
}

main.book-page {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

/*
Title:
font-family: 'Lora', serif;

Subtitle:
font-family: 'Rasa', serif;

Text:
font-family: 'Halant', serif;
*/

body {
  margin: 0 auto;
  font-family: 'Halant', serif;
  font-size: 1.3rem;
}

h1 {
  font-size: 2.5rem;
}

h3 {
  font-size: 2.2rem;
}

h4,
h5 {
  font-size: 1.5rem;
}

h6 {
  font-size: 1.1rem;
}

